import { textTableSize } from './table';



export function frontPage(doc, data) {
    doc.setFontSize(22);
    imgFront(doc);
    textTableSize(doc, `DICTAMEN DE LAS INSTALACIONES ELÉCTRICAS 2022`, 23, 100, 85, 35)
    textTableSize(doc, data.title, 23, 100, 85, 100);
    centerText(doc, data.datePretty, 200);
    textContact(doc);
}

