function textContact(doc) {
    doc.setFontSize(8);
    let yInit = 270;
    let plus = 3.3;
    let x = 140;
    doc.text("www.vivecer.com.mx", x, yInit);
    yInit += plus;
    doc.text("Oficina Cancún (998) 2 72 7760", x, yInit);
    yInit += plus;
    doc.text("Guadalajara Tel. (33) 3804 0790", x, yInit);
    yInit += plus;
    doc.text("WTC Cd. de México Tel. (52) 55 1107 0508", x, yInit);
}

function centerText(doc, text, y) {
    var textWidth = doc.getStringUnitWidth(text) * doc.internal.getFontSize() / doc.internal.scaleFactor;
    var textOffset = (doc.internal.pageSize.width - textWidth) / 2;
    doc.text(textOffset, y, text);
}



