<template>
  <v-card transition="scroll-y-transition">
    <v-toolbar color="rgba(39,55,70,1)" dark flat elevate-on-scroll>
      <v-btn icon @click="back()">
        <v-icon size="23" color="#D5DBDB">arrow_back</v-icon>
      </v-btn>

      <v-toolbar-title>DICTAMEN</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-dialog v-model="dialog" persistent max-width="290">
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-icon size="30" color="#D5DBDB">picture_as_pdf</v-icon>
          </v-btn>
        </template>
        <v-card>
          <v-container>
            <v-card-title class="text-h5" style="color: #273746">
              Descargar
            </v-card-title>
          </v-container>

          <v-card-text>
            <v-row align="center" justify="space-around">
              <v-container>
                <v-btn depressed color="primary" @click="generatePDF()"> Dictamen Eléctrico </v-btn>
              </v-container>
              <v-container>
                <v-btn depressed color="primary" @click="generatePDFHotSpots()">Puntos calientes</v-btn>
              </v-container>
            </v-row>

            <!--    <v-btn outlined> DICTAMEN </v-btn> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn outlined color="#273746" @click="dialog = false">
              Cancelar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <template v-slot:extension>
        <!-- Todos los tabs -->
        <v-tabs v-model="tab" left>
          <v-tabs-slider color="yellow"></v-tabs-slider>
          <v-tab>
            <v-icon>person</v-icon>
          </v-tab>

          <v-tab v-for="(item, i) in section" :key="i">
            <div v-if="item.icon">
              <v-icon> {{ item.tab }}</v-icon>
            </div>
            <div v-else>{{ item.tab }}</div>
          </v-tab>
        </v-tabs>
      </template>
    </v-toolbar>

    <v-tabs-items v-model="tab">
      <!-- Todos los tabs items -->
      <v-tab-item>
        <v-card flat>
          <!-- Detalle del cliente -->
          <client-detail />
        </v-card>
      </v-tab-item>

      <v-tab-item v-for="(item, i) in section" :key="i">
        <component v-bind:is="item.content"></component>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
import { generate } from "@/api/pdf/main";
import { generateHotSpots } from "@/api/pdf/hotspots/index";

import { mapState, mapActions } from "vuex";
export default {
  components: {
    ClientDetail: () => import("@/components/sections/client/ClientDetail.vue"),
    BoardList: () => import("@/components/sections/board/BoardList.vue"),
    DocumentaryInformation: () =>
      import("@/components/sections/DocumentaryInformation.vue"),
    GeneralNonconformities: () =>
      import("@/components/sections/GeneralNonconformities.vue"),
  },
  data() {
    return {
      tab: 0,
      items: [],
      dialog: false,
    };
  },
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),

    section() {
      return this.data.section;
    },
  },
  mounted() {
    this.start();
  },
  methods: {
    ...mapActions({
      getItem: "customers/search",
    }),
    start() {
      this.getItem(Number(this.$route.params.id));
      console.log(this.items);
    },
    back() {
      this.$router.push("/");
    },
    generatePDF() {
      generate(this.data);
    },
    generatePDFHotSpots(){
      generateHotSpots(this.data);
    }
  },
};
</script>